/*
 * This is for global css usage.
 */

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap');
@import url('https://use.typekit.net/qtg4lpb.css');

*,
*::after,
*::before {
	box-sizing: border-box;
	font-family: 'Noto Sans TC', 'PingFangTC-Regular', 'Microsoft JhengHei', 'Roboto', sans-serif;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	outline: none;
}

body,
input,
select,
textarea,
button {
	-webkit-font-smoothing: antialiased;
	font-family: 'Noto Sans TC', 'PingFangTC-Regular', 'Microsoft JhengHei', sans-serif;
}

body {
	margin: 0;
	overflow-x: hidden;
	width: 100%;

	&.no-scroll {
		position: fixed;
		overflow-y: scroll;
	}
}

textarea,
input {
	/* stylelint-disable-next-line property-no-vendor-prefix */
	-webkit-appearance: none;
	border-radius: 0;

	&::placeholder {
		color: var(--light-gray);
	}
}

input {
	width: 100%;
	border: 0;
	font-size: 16px;
	line-height: 1.5;
	letter-spacing: normal;
	color: var(--black);

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
		/* stylelint-disable-next-line property-no-vendor-prefix */
		-webkit-appearance: none;
		margin: 0;
	}

	&:-webkit-autofill {
		/* stylelint-disable-next-line property-no-vendor-prefix */
		-webkit-box-shadow: 0 0 0 30px white inset;
	}

	&[type='number'] {
		/* stylelint-disable-next-line property-no-vendor-prefix */
		-moz-appearance: textfield;
	}
}

a {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
}

@media print {
	@page {
		size: a4 portrait;
		margin: 40px 0;
	}

	html,
	body {
		width: 794px;
		height: 1123px;

		&:global(.no-scroll) {
			position: absolute;

			/* stylelint-disable-next-line declaration-no-important */
			top: 0 !important;
		}

		& > div:first-child {
			display: none;
		}
	}
}

.loading-skeleton {
	background-color: #ededed;
	background: linear-gradient(
			100deg,
			rgba(255, 255, 255, 0) 40%,
			rgba(255, 255, 255, 1) 50%,
			rgba(255, 255, 255, 0) 60%
		)
		#ededed;
	background-size: 200% 100%;
	background-position-x: 180%;
	animation: 1s loading ease-in-out infinite;
}

@keyframes loading {
	to {
		background-position-x: -20%;
	}
}
